import selectEd from '@/static/mbcImg/publish/createBusiness/selectEd.png';
import select from '@/static/mbcImg/publish/createBusiness/select.png';
import skipMaskBg from '@/static/mbcImg/publish/createBusiness/skipMaskBg.png';
export default {
	data() {
		return {
			selectEd: selectEd, // 勾选圆框 黑色
			select: select, // 未勾选圆框 白色
			noTipsImg: select,
			skipMaskBg: skipMaskBg,
			dateLists: this.dateListM
		};
	},
	props: [
		'dateListM'
	],
	// watch: {
	// 	dateListM: {
	// 		handler(a, b) {
	// 			console.log(a, b, '--------------------------------------监听dateListM变化---------------------------------------');
	// 			this.dateLists = a;
	// 		},
	// 		deep: true
	// 	}
	// },
	mounted(){
		console.log(this.dateList, '----------dateList-------------')
	},
	methods: {
		clickMask() {
			this.$emit('tap-SkipMask', [false, null]);
		},
		clickNoTips() {
			if (this.noTipsImg === this.select) {
				this.noTipsImg = this.selectEd;
				uni.setStorageSync('templateFinishSkip', true);
			} else if (this.noTipsImg === this.selectEd) {
				this.noTipsImg = this.select;
				uni.removeStorageSync('templateFinishSkip');
			}
		},
		clickInsistSkip () {
			let params = {}; // 请求总数居时 参数为空
			console.log(this.dateLists, '--------------this.dateLists---------------')
			if (!uni.getStorageSync('landRegist')) {
				this.landRegistra(); // 判断登录状态
			} else if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				params = this.dateLists;
				// 请求总数居时 参数为空
				uni.showLoading({
					// 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/instruc', //接口地址。
					data: params,
					method: 'POST',
					header: {
						Authorization: 'Bearer ' + landRegistLG.token //将token放到请求头中
					},
					success: response => {
						console.log(response.data, '---------------response.data--------------')
						if (response.data.ret === '200') {
							this.$emit('tap-SkipMask', [false, null]);
						} else {
							uni.hideLoading(); // 隐藏 loading
							uni.showToast({
								title: '网络开小差了，请稍后重试',
								icon: 'none',
								duration: 1000
							});
						}
					},
					fail: error => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		}
	}
};
